import { Box, Stack } from '@mui/material';
import classnames from 'classnames';

import { ThemeWrapper } from '@/layout';
import { compose } from '@/helpers';
import { withCasterOptionsStoreWrapper } from '@/store';

import { CasterHeader } from './CasterHeader';
import { CasterFooter } from './CasterFooter';

import styles from './Caster.module.scss';

interface TProps {
  className?: string;
}

export function CasterComponent(props: TProps): JSX.Element {
  const { className } = props;
  // prettier-ignore
  return (
    <ThemeWrapper mode="dark" fullSize>
      <Stack className={classnames(className, styles.container)}>
        <CasterHeader className={styles.topPanel} />
        <Box className={styles.mainBox} flex={1}>
          <Box className={styles.previewWindow} />
        </Box>
        <CasterFooter className={styles.bottomPanel} useSwitchButtons />
        {/*
        <CasterFooter className={styles.bottomPanel} />
        */}
      </Stack>
    </ThemeWrapper>
  );
}

export const Caster = compose(withCasterOptionsStoreWrapper)(
  CasterComponent,
) as typeof CasterComponent;
